import React from "react";
import Item from "./Item";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      list: ["Finish Homework", "Cook Dinner", "Start Project"],
      task: "",
    };
    this.handleChange = this.handleChange.bind(this);
    this.addItem = this.addItem.bind(this);
    this.addItemKey = this.addItemKey.bind(this);
  }

  handleChange(event) {
    this.setState({
      task: event.target.value,
    });
  }

  addItem() {
    const copy = this.state.list;
    copy.push(this.state.task);
    this.setState({
      list: copy,
      task: "",
    });
  }

  addItemKey(event) {
    if (event.keyCode === 13) {
      this.addItem();
    }
  }

  changeItem(index, value) {
    const copy = this.state.list;
    copy[index] = value;
    this.setState({
      list: copy,
    });
  }

  deleteItem(index) {
    const copy = this.state.list;
    copy.splice(index, 1);
    this.setState({
      list: copy,
    });
  }

  render() {
    return (
      <div>
        <nav className="navbar navbar-light bg-primary bg-gradient">
          <span className="navbar-brand mx-auto text-light fs-3 fw-bold">
            React Todo List
          </span>
        </nav>
        <div className="w-80 container mt-4">
          <div className="input-group input-group-lg mb-3">
            <input
              type="text"
              className="form-control"
              placeholder="Enter Task"
              value={this.state.task}
              onChange={this.handleChange}
              onKeyDown={this.addItemKey}
            ></input>
            <button
              type="button"
              class="btn btn-primary"
              onClick={this.addItem}
            >
              Add
            </button>
          </div>
          <hr />
          <div>
            {this.state.list.map((task, index) => (
              <Item
                key={index}
                id={index}
                task={task}
                changeTask={(i, e) => this.changeItem(i, e)}
                deleteTask={(i) => this.deleteItem(i)}
              ></Item>
            ))}
          </div>
        </div>
      </div>
    );
  }
}
